import * as BABYLON from 'babylonjs';

export default class Lights {
    constructor(scene, table_length) {
        this.scene = scene;
        this.lights = [];
        this.shadowGenerators = [];

        let pos = [
            [-1, -1],
            [1, -1],
            [-1, 1],
            [1, 1]
        ]
        for (var i = 0; i < pos.length; i++) {
            let y = table_length / 2;
            let x = pos[i][0] * table_length / 3;
            let z = pos[i][1] * table_length / 6;
            var light = new BABYLON.SpotLight("spotLight" + i, new BABYLON.Vector3(x, y, z), new BABYLON.Vector3(0, -1, 0), Math.PI, 1, scene);

            this.lights.push(light);
            var shadowGenerator = new BABYLON.ShadowGenerator(512, light);
            shadowGenerator.useBlurExponentialShadowMap = true;
            shadowGenerator.useKernelBlur = true;
            shadowGenerator.blurKernel = 64;
            shadowGenerator.forceBackFacesOnly = true;
            shadowGenerator.getShadowMap().refreshRate = BABYLON.RenderTargetTexture.REFRESHRATE_RENDER_ONEVERYFRAME;

            this.shadowGenerators.push(shadowGenerator);
        }
        // new BABYLON.HemisphericLight('light', BABYLON.Vector3.Up(), scene);
    }

    getLights = () => { return this.lights }

    registerShadowObjects = (objects) => {
        for (var gen of this.shadowGenerators) {
            for (var obj of objects) {
                gen.getShadowMap().renderList.push(obj);
            }
        }
    }
}