import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMinus, faPlus } from '@fortawesome/free-solid-svg-icons'

import './slider.css';

export default class InputSlider extends React.Component {
    constructor (props) {
        super(props);
    }

    componentWillReceiveProps = (props) => {
        const {value} = props;
        this.setState({value:value});
    }

    updateValue = (value) => {
        this.setState({value:value})
        this.props.onUpdate(this.props.itemKey, value);
    }

    onChangeValue = (evt) => {
        const value = evt.target.value;
        this.updateValue(value);
    }

    onMinusPressed = () => {
        var currentValue = this.state.value;
        let { min, step } = this.props;
        if (step === undefined) step = 0.1;
        step *= 10;
        currentValue = (currentValue*10 - step)/10;
        if (currentValue <  min) currentValue = min;
        this.updateValue(currentValue);
    }

    onPlusPressed = () => {
        var currentValue = this.state.value;
        let { max, step } = this.props;
        if (step === undefined) step = 0.1;
        step *= 10;
        currentValue = (currentValue*10 + step)/10;
        if (currentValue > max) currentValue = max;
        this.updateValue(currentValue);
    }

    render () {
        const {
            value,
        } = this.props;

        let {
            containerClass,
            label,
            min,
            max,
            unitDom,
            step,
            vertical,
            width,
            sliderStyle
        } = this.props
        if (step === undefined) step = 0.1;
        let sliderClass="slider";
        if (vertical) sliderClass += " vertical";

        return (
            <div className={containerClass} style={{
                width: width?width:'inherit'
            }}>
                <div style={{display:'flex'}}>
                    <span style={{flex:1, textAlign:'center'}}>
                        {label}
                        <span> : {value}{unitDom}</span>
                    </span>
                </div>
                <div style={{display:'flex'}}>
                    <FontAwesomeIcon
                        icon={faMinus}
                        className="btn-change-value"
                        onClick={this.onMinusPressed}
                    />
                    <input
                        type="range"
                        min={min}
                        max={max}
                        value={value}
                        className={sliderClass}
                        style={sliderStyle}
                        step={step}
                        onChange={this.onChangeValue}
                    />
                    <FontAwesomeIcon
                        icon={faPlus}
                        className="btn-change-value"
                        onClick={this.onPlusPressed}
                    />
                </div>
            </div>
        )
    }
}