import * as BABYLON from 'babylonjs';

const alphaOffset = 0;
const betaOffset = -0.035
const MAX_TARGET_LENGTH = 150;
export default class Cameras {
    constructor(scene, canvas, onUpdateTargetCamera) {
        this.scene = scene;
        this.canvas = canvas;

        this.cameras = {
            arcCam: undefined,
            topCam: undefined,
            targetCam: undefined
        }

        this.cameras.arcCam = new BABYLON.ArcRotateCamera("Camera-Arc", 4.44, 1.05, 300, BABYLON.Vector3(0, 10, 0), this.scene);
        this.cameras.arcCam.attachControl(this.canvas);
        this.cameras.arcCam.upperBetaLimit = Math.PI / 2 + betaOffset * 2;
        this.cameras.arcCam.lowerBetaLimit = 0.105;

        this.cameras.arcCam.wheelPrecision = 0.2;
        
        this.cameras.arcCam.upperRadiusLimit = 500;
        this.cameras.arcCam.lowerRadiusLimit = 100;

        this.cameras.targetCam = new BABYLON.ArcRotateCamera("Camera-Target", 4.44, 1.35, MAX_TARGET_LENGTH, BABYLON.Vector3.Zero(), this.scene);
        this.cameras.targetCam.upperRadiusLimit = MAX_TARGET_LENGTH;
        this.cameras.targetCam.lowerRadiusLimit = MAX_TARGET_LENGTH;
        this.cameras.targetCam.upperBetaLimit = Math.PI / 2 + betaOffset * 2;
        this.cameras.targetCam.lowerBetaLimit = 0;
        
        this.cameras.topCam = new BABYLON.ArcRotateCamera("Camera-Top", -Math.PI/2, 0, 330, BABYLON.Vector3.Zero(), this.scene);

        this.cameras.topCam.upperBetaLimit = 0;
        this.cameras.topCam.lowerBetaLimit = 0;

        this.cameras.topCam.upperAlphaLimit = -Math.PI/2;
        this.cameras.topCam.lowerAlphaLimit = -Math.PI/2;

        this.cameras.topCam.upperRadiusLimit = 330;
        this.cameras.topCam.lowerRadiusLimit = 330;

        this.cameras.topCam.mode = BABYLON.Camera.ORTHOGRAPHIC_CAMERA;
        this.cameras.topCam.orthoTop = 150;
        this.cameras.topCam.orthoBottom = -150;
        this.cameras.topCam.orthoLeft = -300;
        this.cameras.topCam.orthoRight = 300;

        this.target = {
            position: undefined,
            rotation: undefined,
            alpha: 4.44,
            beta: 1.35
        }

        this.changeView(this.currentView);

        var _self = this;
        _self.scene.getEngine().runRenderLoop(() => {
            if (scene) {
                var alpha = _self.cameras.targetCam.alpha;
                var beta = _self.cameras.targetCam.beta;
                
                alpha = alpha % (Math.PI * 2);
                if (_self.target.alpha != alpha || _self.target.beta != beta){
                    _self.target.alpha = alpha;
                    _self.target.beta = beta;

                    if (alpha < 0) alpha += Math.PI * 2;
                    typeof onUpdateTargetCamera == 'function' && onUpdateTargetCamera(alpha, Math.PI / 2 - beta + betaOffset);
                }
            }
        });
    }

    changeView (value) {
        var _self = this;
        _self.currentView = value;
        if (value === 'top') {
            _self.scene.activeCamera = _self.cameras.topCam;
            _self.cameras.topCam.attachControl(_self.canvas);
            _self.cameras.arcCam.detachControl(_self.canvas);
            _self.cameras.targetCam.detachControl(_self.cameras);
        } else if (value === 'stand'){
            _self.scene.activeCamera = _self.cameras.arcCam;
            _self.cameras.arcCam.attachControl(_self.canvas);
            _self.cameras.topCam.detachControl(_self.canvas);
            _self.cameras.targetCam.detachControl(_self.canvas);

            _self.cameras.arcCam.setTarget(BABYLON.Vector3.Zero())
        } else if (value === 'targetting') {
            _self.scene.activeCamera = _self.cameras.targetCam;
            _self.cameras.targetCam.attachControl(_self.canvas);
            _self.cameras.arcCam.detachControl(_self.canvas);
            _self.cameras.topCam.detachControl(_self.canvas);
        }
    }

    updateTarget(position, rotation) {
        let _self = this;
        this.target.position = position;
        this.target.rotation = rotation;

        _self.cameras.targetCam.setTarget(
            new BABYLON.Vector3(
                _self.target.position.x,
                _self.target.position.y,
                _self.target.position.z
            )
        )
        _self.cameras.targetCam.alpha = -_self.target.rotation.y
        _self.cameras.targetCam.beta = Math.PI / 2 - _self.target.rotation.z + betaOffset;
    }

    enableControl (value) {
        if (value == false) {
            switch (this.currentView) {
                case 'top':
                    this.cameras.topCam.detachControl(this.canvas);
                    break;
                case 'stand':
                    this.cameras.arcCam.detachControl(this.canvas);
                    break;
                case 'targetting':
                    this.cameras.targetCam.detachControl(this.canvas);
                    break;
                default:
                    break;
            }
        } else {
            switch (this.currentView) {
                case 'top':
                    this.cameras.topCam.attachControl(this.canvas);
                    break;
                case 'stand':
                    this.cameras.arcCam.attachControl(this.canvas);
                    break;
                case 'targetting':
                    this.cameras.targetCam.attachControl(this.canvas);
                    break;
                default:
                    break;
            }
        }
    }
}