import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVideo } from '@fortawesome/free-solid-svg-icons';
import './viewSelector.css';

import viewTop from '../assets/top-view.png';
import viewStand from '../assets/stand-view.png';
import viewTarget from '../assets/target-view.png';

export default class ViewSelector extends Component {
    constructor (props) {
        super(props);
        this.state = {
            showCameraTypes: false
        }
    }

    viewSelectorItemClicked = (item) => {
        if(typeof this.props.viewSelectorItemClicked === 'function'){
            this.props.viewSelectorItemClicked(item);
        }
    }

    render () {
        let _self = this;
        let strTopClass = "view-selector-item";
        let strStandClass = "view-selector-item";
        let strTargetClass = "view-selector-item";

        if (this.props.currentView === 'top') {
            strTopClass += " active";
        } else if (this.props.currentView === 'stand'){
            strStandClass += " active";
        } else if (this.props.currentView === 'targetting'){
            strTargetClass += " active";
        }
        return (
            <div className="view-selector">
                <FontAwesomeIcon
                    className="video-icon"
                    icon={faVideo}
                    onClick={()=>{
                        this.setState({
                            showCameraTypes: !this.state.showCameraTypes
                        })
                    }}
                />
                {
                    this.state.showCameraTypes?(
                        <div className="select-camera-type">
                            <img
                                className={strTopClass}
                                src="data:image/png;base64,"
                                onClick={function () { _self.viewSelectorItemClicked('top') }}
                            />
                            <img
                                className={strStandClass}
                                src="data:image/png;base64,"
                                onClick={function () { _self.viewSelectorItemClicked('stand') }}
                            />
                            <img
                                className={strTargetClass}
                                src="data:image/png;base64,"
                                onClick={function () { _self.viewSelectorItemClicked('targetting') }}
                            />
                        </div>
                    ) : null
                }
            </div>
        )
    }
}