import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faTh,
    faCrosshairs,
    faVolumeMute,
    faBackward,
    faForward,
    faStop,
    faPlay
} from '@fortawesome/free-solid-svg-icons'

import ballImage from './cueball.png';
import InputSlider from './slider';
import './style.css';

const ballDiameter = 52.5;
const maxTipOffset = 15;

export default class ControllerCollection extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            ballImageSize: 180,
            cueLine: Math.round(1800 * this.props.cueLine / Math.PI) / 10,
            cueAngle: Math.round(1800 * this.props.cueAngle / Math.PI) / 10,
            sideEnglish: Math.round(this.props.sideEnglish * 10000) / 10,
            topSpin: Math.round(this.props.topSpin * 10000) / 10,
            speed: Math.round(this.props.speed * 100),
            tipPositionLeft: 0,
            tipPositionTop: 0,
            isPointerDown: false,
        }
        this.domBallImage = new React.createRef();
    }

    checkRadialBounding = (sideEnglish, topSpin) => {
        return (Math.sqrt(Math.pow(sideEnglish, 2) + Math.pow(topSpin, 2)) < maxTipOffset)
    }

    updateTipPosition = e => {
        let x = e.pageX;
        let y = e.pageY;
        let elem = this.domBallImage.current;
        let rect = elem.getBoundingClientRect();
        x -= rect.x;
        y -= rect.y;
        let sideEnglish = (x / this.state.ballImageSize - 0.5) * ballDiameter;
        sideEnglish = Math.round(sideEnglish * 10) / 10;
        let topSpin = (0.5 - y / this.state.ballImageSize) * ballDiameter;
        topSpin = Math.round(topSpin * 10) / 10;

        if (this.checkRadialBounding(sideEnglish, topSpin)){
            let tmp = {
                cueLine: this.state.cueLine,
                cueAngle: this.state.cueAngle,
                sideEnglish: this.state.sideEnglish,
                topSpin: this.state.topSpin,
                speed: this.state.speed
            }
            this.setState({
                tipPositionLeft: x,
                tipPositionTop: y,
            })

            tmp.sideEnglish = sideEnglish;
            tmp.topSpin = topSpin;

            this.dispatchUpdateEvent(tmp);
        } else {
            x -= this.state.ballImageSize/2;
            y -= this.state.ballImageSize/2;
            let dist = Math.sqrt(x*x + y*y);
            
            sideEnglish = maxTipOffset * Math.cos(Math.acos(x/dist));
            sideEnglish = Math.round(sideEnglish * 10) / 10;
            topSpin = maxTipOffset * Math.sin(-Math.asin(y/dist));
            topSpin = Math.round(topSpin * 10) / 10;

            let tipPositionLeft = (sideEnglish / ballDiameter + 0.5) * this.state.ballImageSize;
            let tipPositionTop = (0.5 - topSpin / ballDiameter) * this.state.ballImageSize;

            let tmp = {
                cueLine: this.state.cueLine,
                cueAngle: this.state.cueAngle,
                sideEnglish: this.state.sideEnglish,
                topSpin: this.state.topSpin,
                speed: this.state.speed
            }

            this.setState({
                tipPositionLeft: tipPositionLeft,
                tipPositionTop: tipPositionTop
            })

            tmp.sideEnglish = sideEnglish;
            tmp.topSpin = topSpin;

            this.dispatchUpdateEvent(tmp);
        }
    }

    dispatchUpdateEvent = (items) => {
        let eventValues = items;

        eventValues.cueLine = Math.PI * eventValues.cueLine / 180;
        eventValues.cueAngle = Math.PI * eventValues.cueAngle / 180;
        eventValues.sideEnglish /= 1000;
        eventValues.topSpin /= 1000;
        eventValues.speed /= 100;
        this.props.onControlOptionsUpdated(eventValues);
    }

    componentWillReceiveProps = props => {
        this.setState({
            cueLine: Math.round(1800 * props.cueLine / Math.PI) / 10,
            cueAngle: Math.round(1800 * props.cueAngle / Math.PI) / 10,
            sideEnglish: Math.round(props.sideEnglish * 10000) / 10,
            topSpin: Math.round(props.topSpin * 10000) / 10,
            speed: Math.round(props.speed * 100),
        })
    }

    onInputSliderUpdated = (key, value) => {
        let tmp = Object.assign({}, this.state);
        tmp[key] = parseFloat(value);
        if (key == 'sideEnglish' || key == 'topSpin') {
            if (this.checkRadialBounding(tmp.sideEnglish, tmp.topSpin)) {
                this.dispatchUpdateEvent(tmp);
            }
        } else {
            this.dispatchUpdateEvent(tmp);
        }
    }

    render () {
        const {ballImageSize, tipPositionLeft, tipPositionTop, isPointerDown} = this.state;
        const {cueLine, cueAngle, sideEnglish, topSpin, speed} = this.state;
        const {showGrid, showPath} = this.props;
        var btnGridClass = "toggle-btn";
        var btnShowPathClass = "toggle-btn";
        var btnMuteClass = "toggle-btn";

        if (showGrid === true) btnGridClass += " active";
        if (showPath === true) btnShowPathClass += " active";

        let newTipPositionTop = 0;
        let newTipPositionLeft = 0;

        if (isPointerDown) {
            newTipPositionLeft = tipPositionLeft - 6;
            newTipPositionTop = tipPositionTop - 6;
        } else {
            newTipPositionLeft = (sideEnglish / ballDiameter + 0.5) * ballImageSize - 6;
            newTipPositionTop = (0.5 - topSpin / ballDiameter) * ballImageSize - 6;
        }
        return (
            <div className="controller-collection">
                <div className="common-controller-container">
                    <FontAwesomeIcon
                        className={btnGridClass}
                        icon={faTh}
                        onClick={() => {this.props.onToggleShowGrid(!showGrid)}}
                    />

                    <FontAwesomeIcon
                        className={btnShowPathClass}
                        icon={faCrosshairs}
                        onClick={() => {this.props.onToggleShowPath(!showPath)}}
                    />

                    <FontAwesomeIcon
                        className={btnMuteClass}
                        icon={faVolumeMute}
                    />
                </div>
                <div className="angle-selector-container">
                    <InputSlider
                        label="Cue Line"
                        containerClass="slider-container"
                        min={0}
                        max={360}
                        width={400}
                        value={cueLine}
                        unitDom={<sup>o</sup>}
                        itemKey="cueLine"
                        onUpdate={this.onInputSliderUpdated}
                    />
                    <InputSlider
                        label="Cue Angle"
                        containerClass="slider-container"
                        min={2}
                        max={89}
                        value={cueAngle}
                        unitDom={<sup>o</sup>}
                        itemKey="cueAngle"
                        onUpdate={this.onInputSliderUpdated}
                    />
                </div>
                <div className="tip-controller-container">
                    <div className="tip-position-visualizer" style={{width: ballImageSize}}>
                        <img
                            ref={this.domBallImage}
                            src="data:image/png;base64,"
                            style={{width: ballImageSize}}
                            draggable={false}
                            onPointerUp={e=>{
                                this.setState({
                                    isPointerDown: false
                                })
                                this.updateTipPosition(e)
                            }}
                            onPointerMove={e=>{
                                if(this.state.isPointerDown) {
                                    this.updateTipPosition(e)
                                }
                            }}
                            onMouseDown={e=>{
                                this.setState({
                                    isPointerDown: true
                                })
                                this.updateTipPosition(e)
                            }}
                        />
                        <div
                            className="tip-position"
                            style={{
                                top: newTipPositionTop,
                                left: newTipPositionLeft
                            }}
                        />
                    </div>
                    <InputSlider
                        label="Side English"
                        containerClass="slider-container"
                        min={-15.1}
                        max={15.1}
                        width={ballImageSize}
                        value={sideEnglish}
                        unitDom={<span>mm</span>}
                        itemKey="sideEnglish"
                        onUpdate={this.onInputSliderUpdated}
                    /><br/>
                    <InputSlider
                        label="Top Spin"
                        containerClass="slider-container"
                        min={-15.1}
                        max={15.1}
                        width={ballImageSize}
                        value={topSpin}
                        unitDom={<span>mm</span>}
                        itemKey="topSpin"
                        onUpdate={this.onInputSliderUpdated}
                    />
                </div>
                <div className="play-controller-container">
                    <InputSlider
                        label="Speed"
                        containerClass="slider-container"
                        width={300}
                        min={0}
                        max={450}
                        value={speed}
                        step={1}
                        unitDom={<span>cm/s</span>}
                        itemKey="speed"
                        onUpdate={this.onInputSliderUpdated}
                    />
                    <FontAwesomeIcon
                        className="play-controller"
                        icon={faBackward}
                    />
                    <FontAwesomeIcon
                        className="play-controller"
                        icon={faStop}
                    />
                    <FontAwesomeIcon
                        className="play-controller"
                        icon={faPlay}
                        onClick={this.props.onPlayPressed}
                    />
                    <FontAwesomeIcon
                        className="play-controller"
                        icon={faForward}
                    />
                </div>
            </div>
        )
    }
}