import * as BABYLON from 'babylonjs';

export default class Bat {
    constructor( scene, mesh ) {
        this.scene = scene;
        this.mesh = mesh;

        this.mesh.position.y = 50;
        this.mesh.position.z = 90;

        this.previewPathes = [];
        this.prevCount = 0;

        var _self = this;
        this.collisionObjects = [];

        this.mat = new BABYLON.StandardMaterial("mat1", this.scene);
        this.mat.alpha = 0.5;
        this.mat.diffuseColor = new BABYLON.Color3(1, 1, 1);

        this.scene.getEngine().runRenderLoop(() => {
            if (scene) {
                var collide = false;
                var mesh = _self.mesh;
                for (var obj of _self.collisionObjects) {
                    if (obj.visibility == true && obj.intersectsMesh(mesh, true)) {
                        collide = true;
                        break;
                    }
                }
                if (collide == true) {
                    if ( typeof _self.callbackBatCollide == 'function' ) {
                        _self.callbackBatCollide();
                    }
                }
            }
        });
    }

    getMesh = () =>{ return this.mesh };
    
    update = (position, rotation) => {
        this.mesh.position = position;
        this.mesh.rotation = rotation;
    }

    animateHit = (alpha, beta, dist) => {
        var _self = this;
        const OFFSET = dist * 20;
        var keys = [];

        let currentPos = Object.assign({}, _self.mesh.position);
        let offsetPos = Object.assign({}, currentPos);
        offsetPos.y += OFFSET * Math.sin(beta);
        offsetPos.x += OFFSET * Math.cos(beta) * Math.cos(alpha);
        offsetPos.z += OFFSET * Math.cos(beta) * Math.sin(alpha);

        let lastPos = Object.assign({}, currentPos);
        lastPos.y += -10 * Math.sin(beta);
        lastPos.x += -10 * Math.cos(beta) * Math.cos(alpha);
        lastPos.z += -10 * Math.cos(beta) * Math.sin(alpha);

        keys.push({
            frame: 0,
            value: currentPos
        });

        keys.push({
            frame: 50,
            value: offsetPos
        });

        keys.push({
            frame: 60,
            value: lastPos
        });

        var anim = new BABYLON.Animation("tutoAnimation", "position", 60, BABYLON.Animation.ANIMATIONTYPE_VECTOR3);
        anim.setKeys(keys);

        _self.mesh.animations.push(anim);

        _self.scene.beginAnimation(_self.mesh, 0, 60, false,1, () => {
            _self.updatePreviewPath(false);
        });
    }

    registerCollisions = (items) => {
        this.collisionObjects = items;
    }

    onBatCollide = (callbackBatCollide) => {
        this.callbackBatCollide = callbackBatCollide;
    }

    updatePreviewPath = (visible, data, TABLE_LENGTH, size, height, timeLimit) => {
        if (!timeLimit) timeLimit = 0.3;
        for (var obj of this.previewPathes) {
            obj.dispose();
        }
        if (visible === false) return;
        
        const dataCount = parseInt(data.length / 10000);
        
        for (var index = 0; index < dataCount; index++) {
            let curve = []
            for (var i = index * 10000; i < (index + 1) * 10000; i++) {
                if (data[i] && data[i].time < timeLimit) {
                    curve.push(new BABYLON.Vector3(
                        TABLE_LENGTH * data[i].X / size,
                        height,
                        TABLE_LENGTH * data[i].Y / size
                    ))
                }
            }
            if (curve.length < 2) continue;
            
            var tube = new BABYLON.Mesh.CreateTube("tube"+index, curve, 1, 6, null, 0, this.scene, true, BABYLON.Mesh.FRONTSIDE);
            tube.convertToFlatShadedMesh();
            tube.scaling.y = 0.1;
            tube.material = this.mat;
            this.previewPathes.push(tube)
        }
    }
}