import * as BABYLON from 'babylonjs';

export default class Ball {
    constructor( scene, name, ballRadius, position, texture, visibility ) {
        this.scene = scene;
        this.mesh = BABYLON.Mesh.CreateSphere(name, 16, ballRadius, this.scene);
        this.mesh.position.y = position.y;
        this.mesh.position.x = position.x;
        this.mesh.position.z = position.z;

        
        var matCueBallWhite = new BABYLON.StandardMaterial("redMat", this.scene);
        matCueBallWhite.diffuseTexture = new BABYLON.Texture(texture, this.scene);
        this.mesh.material = matCueBallWhite;

        this.mesh.rotation.y = Math.PI * Math.random() * 2;
        this.mesh.rotation.x = Math.PI * Math.random() * 2;
        this.mesh.rotation.z = Math.PI * Math.random() * 2;

        this.mesh.visibility = visibility;

        this.RAp0 = 0;
        this.RAq0 = 0;
        this.RAr0 = 0;
    }

    getMesh = () => { return this.mesh }
    getPosition = () => { return Object.assign({}, this.mesh.position) }
    getVisibility = () => { return this.mesh.visibility }

    updatePosition = (x, z) => {
        this.mesh.position.x = x;
        this.mesh.position.z = z;
    }

    updateRotation = (RAp, RAq, RAr) => {
        const R = 0.03;
        let Rx = (RAp - this.RAp0) / R
        let Ry = (RAq - this.RAq0) / R
        let Rz = (RAr - this.RAr0) / R

        this.RAp0 = RAp
        this.RAq0 = RAq
        this.RAr0 = RAr

        this.mesh.rotate(BABYLON.Axis.X, -Rx, BABYLON.Space.WORLD);
        this.mesh.rotate(BABYLON.Axis.Y, -Rz, BABYLON.Space.WORLD);
        this.mesh.rotate(BABYLON.Axis.Z, -Ry, BABYLON.Space.WORLD);
    }

    isPotted = () => { return !this.mesh.visibility; }

    pot = () => { this.mesh.visibility = false; }
}