import * as BABYLON from 'babylonjs';
import {
    CAROM_TABLE_LENGTH,
    BALL_SIZE,

    fileCaromTableModel,
    fileBatModel,

} from '../../../assets/constants';

import CBillard from './PhysicsEngine/cBillard';

import Ball from '../../../models/ball';
import Bat from '../../../models/bat';
import Table from '../../../models/table';
import Cameras from '../../../models/cameras';

export default class SceneManager {
    constructor (
        size,
        modelStorePath,
        currentView,
        ballPositions,
        controlOptions,
        onControlOptionUpdate,
        onBatCollide
    ) {
        this.scene = undefined;
        this.canvas = undefined;

        this.size = size;
        this.onControlOptionUpdate = onControlOptionUpdate;
        this.onBatCollide = onBatCollide;

        this.ballRadius = 400 * BALL_SIZE / (this.size * 100);

        this.modelStorePath = modelStorePath;

        this.initBallPosition = ballPositions;
        this.ballPositions = {
            posCueBallWhite: {
                x: CAROM_TABLE_LENGTH * ballPositions.posCueBallWhite.x / size,
                z: CAROM_TABLE_LENGTH * ballPositions.posCueBallWhite.z / size
            },
            posCueBallYellow: {
                x: CAROM_TABLE_LENGTH * ballPositions.posCueBallYellow.x / size,
                z: CAROM_TABLE_LENGTH * ballPositions.posCueBallYellow.z / size
            },
            posTargetBall: {
                x: CAROM_TABLE_LENGTH * ballPositions.posTargetBall.x / size,
                z: CAROM_TABLE_LENGTH * ballPositions.posTargetBall.z / size
            }
        };

        this.currentView = currentView;

        this.cameras = undefined;

        this.objects = {
            table: undefined,
            bat: undefined,
            cueBallWhite: undefined,
            cueBallYellow: undefined,
            targetBall: undefined,
            batContactLine: undefined,
            batLookatPoint: undefined,
            directionLine: undefined,
        };

        this.controlOptions = controlOptions;

        this.grids = [];

        this.pathCueWhite = undefined;
        this.pathCueYellow = undefined;
        this.pathTargetBall = undefined;

        this.billard = new CBillard(
            310,
            undefined,
            300,
        );
        
        this.timerCount = 0;
        this.bShowBallPath = false;

        this.RAp0 = [0, 0, 0]
        this.RAq0 = [0, 0, 0]
        this.RAr0 = [0, 0, 0]
    }

    drawBallPath = (value) => {
        this.bShowBallPath = value;
        let res = this.pathData

        if(this.pathCueWhite){
            this.pathCueWhite.dispose()
        } 
        
        if(this.pathCueYellow){
            this.pathCueYellow.dispose()
        }

        if(this.pathTargetBall){
            this.pathTargetBall.dispose()
        } 

        if(!res || res.length == 0) return;
        
        if (value) {
            var whiteBallPoints = [];
            for (var i = 0; i < 10000; i++) {
                if (res[i]) {
                    whiteBallPoints.push(new BABYLON.Vector3(
                        CAROM_TABLE_LENGTH * res[i].X / this.size,
                        this.ballRadius / 2,
                        CAROM_TABLE_LENGTH * res[i].Y / this.size
                    ))
                }
            }

            var matWhiteBallPath = new BABYLON.StandardMaterial("mat1", this.scene);
            matWhiteBallPath.alpha = 0.5;
            matWhiteBallPath.diffuseColor = new BABYLON.Color3(1, 1, 1);
            
            this.pathCueWhite = new BABYLON.Mesh.CreateTube("tube-white", whiteBallPoints, 1, 6, null, 0, this.scene, true, BABYLON.Mesh.FRONTSIDE);
            this.pathCueWhite.convertToFlatShadedMesh();
            this.pathCueWhite.scaling.y = 0.1;
            this.pathCueWhite.material = matWhiteBallPath;

            var yellowBallPoints = [];
            for (i = 10000; i < 20000; i++) {
                if (res[i]) {
                    yellowBallPoints.push(new BABYLON.Vector3(
                        CAROM_TABLE_LENGTH * res[i].X / this.size,
                        this.ballRadius / 2,
                        CAROM_TABLE_LENGTH * res[i].Y / this.size
                    ));
                }
            }

            var matYellowBallPath = new BABYLON.StandardMaterial("mat1", this.scene);
            matYellowBallPath.alpha = 0.5;
            matYellowBallPath.diffuseColor = new BABYLON.Color3(0.8, 0.8, 0);

            this.pathCueYellow = new BABYLON.Mesh.CreateTube("tube-white", yellowBallPoints, 1, 6, null, 0, this.scene, true, BABYLON.Mesh.FRONTSIDE);
            this.pathCueYellow.convertToFlatShadedMesh();
            this.pathCueYellow.scaling.y = 0.1;
            this.pathCueYellow.material = matYellowBallPath;

            var redBallPoints = [];

            for (i = 20000; i < 30000; i++) {
                if (res[i]) {
                    redBallPoints.push(new BABYLON.Vector3(
                        CAROM_TABLE_LENGTH * res[i].X / this.size,
                        this.ballRadius / 2,
                        CAROM_TABLE_LENGTH * res[i].Y / this.size
                    ));
                }
            }

            var matRedBallPath = new BABYLON.StandardMaterial("mat1", this.scene);
            matRedBallPath.alpha = 0.5;
            matRedBallPath.diffuseColor = new BABYLON.Color3(0.8, 0, 0);

            this.pathTargetBall = new BABYLON.Mesh.CreateTube("tube-white", redBallPoints, 1, 6, null, 0, this.scene, true, BABYLON.Mesh.FRONTSIDE);
            this.pathTargetBall.convertToFlatShadedMesh();
            this.pathTargetBall.scaling.y = 0.1;
            this.pathTargetBall.material = matRedBallPath;
        }
    }

    configCameras = () => {
        const _self = this;
        _self.cameras = new Cameras(_self.scene, _self.canvas, (alpha, beta) => {
            _self.controlOptions.cueLine = alpha;
            _self.controlOptions.cueAngle = beta;
            if (typeof _self.onControlOptionUpdate === 'function') _self.onControlOptionUpdate(_self.controlOptions);
        });

        _self.changeView(_self.currentView);
    }

    loadModels = () => {
        const _self = this;
        const { modelStorePath } = this;

        BABYLON.SceneLoader.ImportMesh("", modelStorePath, fileBatModel, _self.scene, function (newMeshes) {
            _self.objects.bat = new Bat(_self.scene, newMeshes[0]);
            
            BABYLON.SceneLoader.ImportMesh("", modelStorePath, fileCaromTableModel, _self.scene, function (newMeshes) {
                _self.objects.table = new Table(_self.scene, newMeshes, modelStorePath);
                
                _self.configCueBalls();
                _self.updateBat(_self.controlOptions);
            });
        });
    }

    updateControlOptions = (data) => {
        this.controlOptions = data;
        if (this.t0 == undefined) {
            this.updateBat(data);
        }
    }

    updateBat = (data, flag) => {
        const {
            cueLine,
            cueAngle,
            sideEnglish,
            topSpin,
        } = data;
        const {size, ballRadius, cameras} = this;
        const {bat, cueBallWhite} = this.objects;

        let deltaSideEnglish = CAROM_TABLE_LENGTH * sideEnglish / size;
        let deltaTopSpin = CAROM_TABLE_LENGTH * topSpin / size;

        let pos = cueBallWhite.getPosition();

        bat.update(
            {
                x: pos.x - deltaSideEnglish * Math.sin(cueLine) - deltaTopSpin * Math.sin(cueAngle) * Math.cos(cueLine),
                z: pos.z + deltaSideEnglish * Math.cos(cueLine) - deltaTopSpin * Math.sin(cueAngle) * Math.sin(cueLine),
                y: ballRadius/2 + deltaTopSpin * Math.cos(cueAngle)
            },
            {
                x: 0,
                y: -cueLine,
                z: Math.PI/2 + cueAngle
            }
        );

        bat.updatePreviewPath(true, this.getSimulationRusult(), CAROM_TABLE_LENGTH, this.size, this.ballRadius/2, 0.3);

        if(!flag) {
            cameras.updateTarget(
                {
                    x: pos.x - deltaSideEnglish * Math.sin(cueLine) - deltaTopSpin * Math.sin(cueAngle) * Math.cos(cueLine),
                    z: pos.z + deltaSideEnglish * Math.cos(cueLine) - deltaTopSpin * Math.sin(cueAngle) * Math.sin(cueLine),
                    y: ballRadius/2 + deltaTopSpin * Math.cos(cueAngle)
                },
                {
                    x: 0,
                    y: -cueLine,
                    z: cueAngle
                }
            )
            cameras.changeView(this.currentView)
        }
    }

    generateReflectionMaterial = (root, others) => {
        let {scene} = this;
        const reflectRate = 0.1;
		
		var probe = new BABYLON.ReflectionProbe("satelliteProbe" + root.name, 512, scene);
		for (var index = 0; index < others.length; index++) {
			probe.renderList.push(others[index]);			
		}
		
        root.material.reflectionTexture = probe.cubeTexture;
        root.material.reflectivityColor = new BABYLON.Color3(reflectRate, reflectRate, reflectRate);
		
		root.material.reflectionFresnelParameters = new BABYLON.FresnelParameters();
        root.material.reflectionFresnelParameters.bias = 0;
		probe.attachToMesh(root);
    }
    
    configCueBalls = () => {
        const _self = this;
        const {
            modelStorePath,
            ballRadius
        } = this;

        const { table, bat } = this.objects;
        let {
            posCueBallWhite,
            posCueBallYellow,
            posTargetBall
        } = this.ballPositions;

        _self.objects.cueBallWhite = new Ball(
            _self.scene,
            "cue-ball-white",
            ballRadius,
            {
                x: posCueBallWhite.x,
                y: ballRadius/2,
                z: posCueBallWhite.z,
            },
            modelStorePath + "cueball-white.jpg",
            true
        )

        _self.objects.cueBallYellow = new Ball(
            _self.scene,
            "cue-ball-yellow",
            ballRadius,
            {
                x: posCueBallYellow.x,
                y: ballRadius/2,
                z: posCueBallYellow.z,
            },
            modelStorePath + "cueball-yellow.jpg",
            true
        )

        _self.objects.targetBall = new Ball(
            _self.scene,
            "cue-ball-red",
            ballRadius,
            {
                x: posTargetBall.x,
                y: ballRadius/2,
                z: posTargetBall.z,
            },
            modelStorePath + "targetball.jpg",
            true
        )

        _self.generateReflectionMaterial(_self.objects.cueBallWhite.getMesh(),[
            _self.objects.cueBallYellow.getMesh(),
            _self.objects.targetBall.getMesh()
        ].concat(table.getMeshes()));

        _self.generateReflectionMaterial(_self.objects.cueBallYellow.getMesh(),[
            _self.objects.cueBallWhite.getMesh(),
            _self.objects.targetBall.getMesh()
        ].concat(table.getMeshes()))

        _self.generateReflectionMaterial(_self.objects.targetBall.getMesh(),[
            _self.objects.cueBallWhite.getMesh(),
            _self.objects.cueBallYellow.getMesh()
        ].concat(table.getMeshes()))


        // register collisions for bat
        let collisionObjects = [];
        collisionObjects.push(
            _self.objects.cueBallYellow.getMesh(),
            _self.objects.targetBall.getMesh()
        )
        for (var obj of table.getMeshes()) {
            if (
                obj.name == 'rail-1'
                || obj.name == 'rail-2'
                || obj.name == 'rail-3'
                || obj.name == 'rail-4'
                // || obj.name == 'carpet-side'
            ) collisionObjects.push(obj);
        }
        bat.registerCollisions(collisionObjects);

        bat.onBatCollide(() => {
            if (typeof _self.onBatCollide == 'function') _self.onBatCollide();
        })
        // end register collisions for bat
    }

    configGrids = () => {
        const _self = this;
        const grayRate = 0.7;
        const color = [new BABYLON.Color4(grayRate,grayRate,grayRate,1), new BABYLON.Color4(grayRate,grayRate,grayRate,1)];
        for (var i = 0; i < 15; i++) {
            let gridPoints = [
                new BABYLON.Vector3(-CAROM_TABLE_LENGTH*7/16 + i * CAROM_TABLE_LENGTH/16, 2, -CAROM_TABLE_LENGTH/4),
                new BABYLON.Vector3(-CAROM_TABLE_LENGTH*7/16 + i * CAROM_TABLE_LENGTH/16, 2, CAROM_TABLE_LENGTH/4)
            ];
            _self.grids.push(BABYLON.MeshBuilder.CreateLines("lines", {points: gridPoints, colors: color}, _self.scene));
        }
        for (i = 0; i < 7; i++) {
            let gridPoints = [
                new BABYLON.Vector3(-CAROM_TABLE_LENGTH/2, 2, -CAROM_TABLE_LENGTH*3/16 + i * CAROM_TABLE_LENGTH/16),
                new BABYLON.Vector3(CAROM_TABLE_LENGTH/2, 2, -CAROM_TABLE_LENGTH*3/16 + i * CAROM_TABLE_LENGTH/16)
            ];
            _self.grids.push(BABYLON.MeshBuilder.CreateLines("lines", {points: gridPoints, colors: color}, _self.scene));
        }
        this.toggleGrids(false);
    }

    toggleGrids = (visible) => {
        const {grids} = this;
        for (var i = 0; i < grids.length; i++) {
            grids[i].visibility = visible;
        }
    }

    initializeScene = (scene, canvas) => {
        let _self = this;
        _self.scene = scene;
        _self.canvas = canvas;

        _self.scene.clearColor = new BABYLON.Color3(0.05, 0.05, 0.05);

        new BABYLON.HemisphericLight('light', BABYLON.Vector3.Up(), scene);

        _self.configCameras();
        _self.loadModels();
        _self.configGrids();

        _self.scene.getEngine().runRenderLoop(() => {
            if (scene) {
                scene.render();
                _self.animateBalls();
            }
        });

        canvas.addEventListener("pointerdown", _self.onPointerDown, false);
        canvas.addEventListener("pointerup", _self.onPointerUp, false);
        canvas.addEventListener("pointermove", _self.onPointerMove, false);
    }

    getGroundPosition = () => {
        let {scene} = this;
        var pickinfo = scene.pick(scene.pointerX, scene.pointerY, function (mesh) { return mesh.name == 'carpet'; });
        if (pickinfo.hit) {
            return pickinfo.pickedPoint;
        }

        return null;
    }

    onPointerDown = (evt) => {
        let _self = this;
        let {scene} = this;
        if (evt.button !== 0) {
            return;
        }

        // check if we are under a mesh
        var pickInfo = scene.pick(scene.pointerX, scene.pointerY, function (mesh) {return (mesh.name === 'cue-ball-white' || mesh.name === 'cue-ball-yellow' || mesh.name === 'cue-ball-red')});
        if (pickInfo.hit) {
            _self.currentMesh = pickInfo.pickedMesh;
            _self.startingPoint = _self.getGroundPosition(evt);

            if (_self.startingPoint) {
                setTimeout(function () {
                    _self.cameras.enableControl(false);
                }, 0);
            }
        }
    }

    onPointerUp = () => {
        let _self = this;
        let {startingPoint} = this;
        if (startingPoint) {
            _self.cameras.enableControl(true);
            _self.startingPoint = null;
            _self.currentMesh = null;
            return;
        }
    }

    onPointerMove = (evt) => {
        let _self = this;
        let {startingPoint, currentMesh} = this;
        if (!startingPoint) {
            return;
        }

        var current = _self.getGroundPosition(evt);

        if (!current) {
            return;
        }

        if (Math.abs(current.x) >= (CAROM_TABLE_LENGTH / 2 - this.ballRadius/2) || Math.abs(current.z) >= (CAROM_TABLE_LENGTH / 4 - this.ballRadius/2)) return;
        
        currentMesh.position = current;
        currentMesh.position.y = _self.ballRadius/2

        startingPoint = current;
        _self.updateBat(_self.controlOptions);
    }

    changeView (view) {
        const _self = this;
        _self.currentView = view;
        _self.cameras.changeView(view);
    }

    moveAndRotateBall = (obj, t, tmp, ) => {
        let range = 10000;
        let index = -1;
        let _self = this;
        let i;
        for(i = tmp * range ; i < (tmp + 1) * range; i++){
            if(_self.pathData[i])
            {
                if(_self.pathData[i].time < (t - _self.t0)){
                    index = i;
                }
            } else { break;}
        }
        if(index >= 0){
            let data = _self.pathData[index];

            let posX = CAROM_TABLE_LENGTH * data.X / _self.size;
            let posY = CAROM_TABLE_LENGTH * data.Y / _self.size;

            obj.updatePosition(posX, posY);
            
            obj.updateRotation(
                _self.pathData[index].RAp,
                _self.pathData[index].RAq,
                _self.pathData[index].RAr,
            )
            
            if (index == i-1) {
                return false;
            }
            return true;
        }
    }
    
    animateBalls = () => {
        let _self = this;
        let { cueBallWhite, cueBallYellow, targetBall } = this.objects;
        if(!_self.pathData || _self.pathData.length == 0) return;
        if (_self.t0 == undefined) return;
        let t = new Date().getTime() / 1000;

        var res1 = this.moveAndRotateBall(cueBallWhite, t, 0);
        var res2 = this.moveAndRotateBall(cueBallYellow, t, 1);
        var res3 = this.moveAndRotateBall(targetBall, t, 2);
        if (res1 == false && res2 == false && res3 == false) {
            _self.pathData = [];
            _self.updateBat(_self.controlOptions);
            _self.t0 = undefined;
        }
    }

    getSimulationRusult = () => {
        var _self = this;
        const {controlOptions} = this;
        const {
            cueBallWhite,
            cueBallYellow,
            targetBall
        } = this.objects;

        return _self.billard.Simuler(
            1,
            {
                Xinit: [
                    _self.size * cueBallWhite.getPosition().x / CAROM_TABLE_LENGTH,
                    _self.size * cueBallYellow.getPosition().x / CAROM_TABLE_LENGTH,
                    _self.size * targetBall.getPosition().x / CAROM_TABLE_LENGTH
                ],
                Yinit: [
                    _self.size * cueBallWhite.getPosition().z / CAROM_TABLE_LENGTH,
                    _self.size * cueBallYellow.getPosition().z / CAROM_TABLE_LENGTH,
                    _self.size * targetBall.getPosition().z / CAROM_TABLE_LENGTH
                ],
                RApInit: [0, 0, 0],
                RAqInit: [0, 0, 0],
                RArInit: [0, 0, 0],
                el: controlOptions.sideEnglish,
                er: controlOptions.topSpin,
                alphaQ: controlOptions.cueAngle,
                betaQ: controlOptions.cueLine,
                Vqueue:controlOptions.speed,
            }
        );
    }
    playSimulation = () => {
        if (this.t0) return;
        var _self = this;
        const {controlOptions} = this;
        const {
            bat
        } = this.objects;
        bat.animateHit(controlOptions.cueLine, controlOptions.cueAngle, controlOptions.speed);
        setTimeout(() => {
            var res = _self.getSimulationRusult();

            if(!res || res.length == 0) return;
            _self.pathData = res;
            _self.drawBallPath(_self.bShowBallPath)

            _self.t0 = new Date().getTime() / 1000;
        }, 850);
    }
}