import React from 'react';

import ViewSelector from '../../view/viewSelector';
import SimulatorEngine from './simulatorEngine';
import ControllerCollection from '../../view/controller';

import './styles.css';

class PoolSimulator extends React.Component {
    constructor () {
        super();

        this.state = {
            currentView : 'top',
            showGrid : false,
            showPath: false,
            size: 310,
            cueLine: 30/180*Math.PI,
            cueAngle: 5/180*Math.PI,
            sideEnglish: -0.0087,
            topSpin: 0.0075,
            speed: 3.80,
            points: {
                Xinit: [],
                Yinit: [],
                empocheeInit: []
            }
        }
        this.simulator = React.createRef();
    }
    componentWillMount = () => {
        this.updateSimulateInfo();
    }
    updateSimulateInfo = () => {
        var info = [
            'Stephan COHEN',
            '...',
            '0',
            '280',
            '9',
            '1',
            '0.09016666666666652',
            '-0.15416666666666667',
            '0',
            '-0.7000000000000001',
            '0',
            '1',
            '-0.6000000000000001',
            '0',
            '1',
            '-0.5',
            '0',
            '1',
            '-0.4',
            '0',
            '1',
            '-0.30000000000000004',
            '0',
            '1',
            '1.1278562236304084',
            '0.5949669539121291',
            '0',
            '-0.7158538054923216',
            '0.28840047307995464',
            '0',
            '0.09842832940320168',
            '-0.3689576159836725',
            '0',
            '0.6413658575465282',
            '-0.03231798897590485',
            '0',
            '0.2',
            '0',
            '1',
            '0.30000000000000004',
            '0',
            '1',
            '0.4',
            '0',
            '1',
            '0.5',
            '0',
            '1',
            '0.6000000000000001',
            '0',
            '1',
            '0.7000000000000001',
            '0',
            '1',
            '1.8639999999999999',
            '0.03490658503988659',
            '-2.4890317318711697',
            '0.014000000000000002',
            '0.004999999999999999',
        ];
        let tmp = {
            size: 2.54,
            points: {
                Xinit: [],
                Yinit: [],
                empocheeInit: []
            }
        }

        // var nb_points = info[5];
        var _loc_6 = 6;

        var _loc_3 = 0;
        while (_loc_3 < 16)
        {
            tmp.points.Xinit[_loc_3] = info[_loc_6];
            _loc_6++;
            tmp.points.Yinit[_loc_3] = info[_loc_6];
            _loc_6++;
            if (info[_loc_6] == 0)
            {
                tmp.points.empocheeInit[_loc_3] = false;
            }
            else
            {
                tmp.points.empocheeInit[_loc_3] = true;
            }
            _loc_6++;
            _loc_3++;
        }
        tmp.speed = parseFloat(info[_loc_6]);
        _loc_6++;
        tmp.cueAngle = parseFloat(info[_loc_6]);
        _loc_6++;
        tmp.cueLine = parseFloat(info[_loc_6]);
        if (tmp.cueLine < 0) tmp.cueLine += Math.PI * 2
        _loc_6++;
        tmp.sideEnglish = parseFloat(info[_loc_6]);
        _loc_6++;
        tmp.topSpin = parseFloat(info[_loc_6]);
        this.setState(tmp)
    }

    viewSelectorItemClicked = (item) => {
        this.setState({currentView: item});
        this.simulator.current.changeView(item);
    }

    onToggleShowGrid = (value) => {
        this.setState({showGrid: value})
        this.simulator.current.toggleGrids(value);
    }

    onToggleShowPath = (value) => {
        this.setState({showPath: value})
        this.simulator.current.drawBallPath(value);
    }

    onPlayPressed = () => {
        this.simulator.current.playSimulation();
    }

    onControlOptionsUpdated = (data) => {
        this.setState({...data});
        this.simulator.current.updateControlOptions(data);
    }

    componentDidMount = () => {
    }

    render () {
        const {
            currentView,

            showGrid,
            showPath,

            size,
            cueLine,
            cueAngle,
            sideEnglish,
            topSpin,
            speed,

            points
        } = this.state;
        return (
            <div style={{position: 'relative'}}>
                <ViewSelector
                    currentView={currentView}
                    viewSelectorItemClicked={this.viewSelectorItemClicked}
                />
                <SimulatorEngine
                    modelStorePath={this.props.modelStorePath}
                    ref={this.simulator}

                    size={size}
                    currentView={currentView}
                    cueLine = {cueLine}
                    cueAngle = {cueAngle}
                    sideEnglish = {sideEnglish}
                    topSpin = {topSpin}
                    speed = {speed}

                    points = {points}
                    onControlOptionUpdate = {(controlOption) => {
                        this.setState({
                            cueAngle: controlOption.cueAngle,
                            cueLine: controlOption.cueLine,
                            sideEnglish: controlOption.sideEnglish,
                            speed: controlOption.speed,
                            topSpin: controlOption.topSpin,
                        })
                    }}
                />
                <ControllerCollection
                    showGrid={showGrid}
                    showPath={showPath}
                    cueLine = {cueLine}
                    cueAngle = {cueAngle}
                    sideEnglish = {sideEnglish}
                    topSpin = {topSpin}
                    speed = {speed}

                    onControlOptionsUpdated={this.onControlOptionsUpdated}

                    onToggleShowGrid={this.onToggleShowGrid}
                    onToggleShowPath = {this.onToggleShowPath}
                    onPlayPressed = {this.onPlayPressed}
                />
            </div>
        )
    }
}

export default PoolSimulator;