import * as BABYLON from 'babylonjs';

export default class Table {
    constructor (scene, meshes, modelStorePath) {
        this.scene = scene;
        this.meshes = meshes;

        for (var obj of this.meshes) {
            if (obj.name == 'carpet') {
                obj.receiveShadows = true;
                obj.material.bumpTexture = new BABYLON.Texture(modelStorePath + 'carpet-specular.png', this.scene);
                obj.material.bumpTexture.level = 0.1;
                obj.material.bumpTexture.uScale = 1;
                obj.material.bumpTexture.vScale = 1;
            }
        }
    }

    getMeshes = () => { return this.meshes }
}